
.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width:100%;
    height:600px;
    background:#ffffff;
    background-image: url("https://i.ibb.co/vcHxPx7/Untitled-design-5.png");
    background-size:80%;
    background-position-x: 60%;
    background-position-y: 10%;
    background-repeat: no-repeat;
    object-fit: cover;
    border: 1px solid black;
}

.jamieImg{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.showcaseImg{
    display: flex;
    align-items: center;
    justify-content: center;
    width:500px;
    border: 5px solid black;
    box-shadow: 0 0 10px 0;
}

.showcaseTitle{
    display: flex;
    width:500px;
    font-size: 50px;
    padding-bottom: 100px;
    align-items: center;
    justify-content: center;
}
