.jamiePP{
    width:200px;
    border-radius: 50%;
    box-shadow: 0 4px 4px 4px grey;
}

.nameTitle{
    margin-top: 2vw;
    font-family: Georgia;
    letter-spacing: .3vw;
    font-weight: bold;
    font-size: 3.7vw;
}


.jobTitle{
    font-family: Verdana;
    font-weight: 100;
    color: black;
    font-size:1.3vw;
    margin-bottom: 100px;
}

.header{
    display:none;
    position:fixed;
    width:100%;
    height:70px;
    background:#6D70C6;
    z-index: 100;
}

